/* 顶部展开的3级菜单标签 */
.top_ {
  margin: 5px 0px 5px 5px;
}
.dis_flex_ {
  display: flex;
}
/* 主要内容区域布局 */
.main_ {
  /* width: 100%; */
  margin: 0px 5px 5px 5px;
  border: 1px solid #eaefef;
  background-color: #fff;
  overflow-x: hidden;
}
/* 搜索软件，搜索算例一行左侧边距 */
.outside_input_ {
  padding-top: 5px;
  margin-left: 10px;
  height: 44px;
}
/* 搜索软件框样式 */
.search_software_ {
  float: left;
  width: 23%;
}
/* 搜索算例框样式 */
.search_CalcExam_ {
  float: right;
  width: 165px;
  margin-right: 20px;
}

/* 模块列表 */
.software_list_ {
  width: 35%;
  max-width: 500px;
  min-width: 390px;
  margin: 5px 5px 5px 5px;
  border: 1px solid #eaefef;
  user-select: none;
}
/* 模块列表的每行样式 */
.software_list_out_ {
  width: 100%;
  height: 25px;
}
/* 模块列表缩略图图片 */
.software_list_img_ {
  cursor: pointer;
  float: left;
  margin-right: 15px;
}

.tooltip_briefDescription_ {
  margin: 0px;
}

.tooltip_briefDescription_content_ {
  white-space: pre;
}

.briefDescription_ {
  margin: 0px;
}

.briefDescription_content_ {
  white-space: pre;
}

/* 图片的透明遮罩层 */
.img-flow {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 55px;
  height: 41.25px;
  background: #444;
  /* 透明度 */
  opacity: 0.3;
}
.img-flow:hover {
  opacity: 0.5;
}
.img-run-png {
  position: absolute;
  top: 20px;
  left: 28px;
  opacity: 0.3;
  font-size: 28px;
  /* color:rgb(63, 56, 55); */
  color: black;
}
.img-run-png:hover {
  cursor: pointer;
  opacity: 0.7;
}
/* 模块的状态图标 */
.software_list_empower_ {
  float: left;
  /* cursor: pointer; */
}

.software_list_empower_:hover {
  color: #000000;
}
/* 模块的状态图标 */
.software_list_empower_icon_ {
  float: left;
  /* cursor: pointer; */
}
/* 模块名字，购买详情收藏的整体布局 */
.software_list_text_ {
  float: left;
}
/* 模块名字 */
.software_list_name_ {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: bold;
  width: 100%;
  color: #000000;
  text-overflow: ellipsis;
}
.software_list_filemgr_ {
  float: right;
  cursor: pointer;
  margin-right: 4px;
  overflow: hidden;
  line-height: 41.25px;
}
/* 购买详情收藏的整体布局 */
.software_list_one_ {
  overflow: hidden;
  width: 100%;
  height: 10%;
}
/* 靠左浮动，指针变成手（购买，详情，收藏单独使用） */
.software_list_two_ {
  float: left;
  cursor: pointer;
  margin-right: 8px;
}
/* 鼠标悬浮在购买，详情，收藏上方字体变黑色 */
.software_list_two_:hover {
  /* color: #000000; */
  font-size: 15px;
  color: #0681ec;
  font-weight: bold;
}
.defFontStyle {
  color: rgb(209, 7, 7);
}
.freeStyle_ {
  color: rgb(8, 92, 8);
}
.detail_ {
  color: rgb(56, 145, 197);
}
.collect_ {
  color: rgb(218, 115, 75);
}
.filemgr_ {
  color: rgb(218, 115, 75);
}
.font-bold {
  font-weight: "";
}
/* 文件列表 */
.file_list_ {
  flex: 1;
  margin: 5px 5px 5px 0px;
  border: 1px solid #eaefef;
}
.software-list-height {
  height: 579px;
  overflow-x: hidden;
  overflow-y: auto;
}
.file-list-height {
  height: 579px;
  overflow-x: hidden;
  overflow-y: auto;
}
.multiOperate {
  margin-left: 347px;
}

.el-table__header tr,
.el-table__header th {
  padding: 0;
  height: 71px;
}

.el-table__body tr,
.el-table__body td {
  height: 71px;
}

.software-list-height:hover {
  overflow-y: auto;
  overflow-x: hidden;
}
.software-list-height:not(:hover) {
  overflow: hidden;
}
.software-list-height::-webkit-scrollbar-thumb {
  background-color: rgb(170, 170, 170);
  -moz-border-radius: 2em;
  border-radius: 2em;
}
.file-list-height:hover {
  overflow-y: auto;
  overflow-x: hidden;
}
.file-list-height:not(:hover) {
  overflow: hidden;
}
.file-list-height::-webkit-scrollbar-thumb {
  background-color: rgb(170, 170, 170);
  -moz-border-radius: 2em;
  border-radius: 2em;
}
.fuzzy-search-return {
  cursor: pointer;
  font-size: 14px;
}
.fuzzy-search-return:hover {
  color: rgb(4, 128, 0);
  /* font-size: larger; */
}
.ht_tooltip {
  margin: 4px;
}
.banner {
  display: flex;
}
